// Colores del módulo
$c_linea_gris: #dedede;

@import "../base/tipografias";
@import "../base/colores";
@import "../utils/mixins";
/*---------------------------------------------------------------------------*
 * MainNav
----------------------------------------------------------------------------*/
.MainNav {
  &,
  & * {
    box-sizing: border-box;
  }

  &-list {
    padding-top: 5px;
    padding-left: 5px;
    border-bottom: 1px solid $c_linea_gris;
    @include breakpoint(phablet) {
      padding-left: 20px;
    }
    &:after {
      clear: both;
      font-size: 0;
      height: 0;
      content: " ";
      display: block;
    }
  }
  &-item {
    background-color: transparent;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-left: 1px solid transparent;
    border-top: 1px solid transparent;
    border-right: 1px solid transparent;
    bottom: -1px;
    cursor: pointer;
    float: left;
    font-weight: $w_light;
    font-size: 13px;
    height: 32px;
    padding: 0 5px;
    display: table;
    text-align: center;
    color: $c_texto_base;
    line-height: 1;
    width: 135px;
    position: relative;
    z-index: 100;
    @include breakpoint(phablet) {
      font-size: 15px;
      width: 210px;
    }
    &.is-active {
      background-color: #fff;
      border-color: $c_linea_gris;
      color: $c_texto_base_azul;
      font-weight: 500;
    }
    span {
      display: table-cell;
      vertical-align: middle;
    }
  }
}
/*---------------------------------------------------------------------------*
 * Fin:MainNav
----------------------------------------------------------------------------*/
